.burger {
  background-color: #7557FF;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 100%;
  cursor: pointer;
  position: relative;
  z-index: 100;
}
.burger__line {
  display: block;
  width: 14px;
  height: 2px;
  background-color: #FFFFFF;
  transition: background-color 0.2s;
  position: relative;
}
.burger__line::before, .burger__line::after {
  content: "";
  display: block;
  width: 14px;
  height: 2px;
  background-color: #FFFFFF;
  transition: all 0.2s;
  position: absolute;
  left: 0;
}
.burger__line::before {
  top: -6px;
}
.burger__line::after {
  bottom: -6px;
}
.burger:hover {
  background-color: #373042;
}
.burger.active .burger__line {
  background-color: transparent;
}
.burger.active .burger__line::before {
  top: 0;
  transform: rotate(45deg);
}
.burger.active .burger__line::after {
  bottom: 0;
  transform: rotate(-45deg);
}

@media (min-width: 1160px) {
  .burger {
    display: none;
  }
}
