.link {
  position: relative;
  color: #7557FF;
  font-family: "CoFo Sans", "Arial", sans-serif;
  font-weight: 500;
  font-size: 20px;
  transition: color 0.2s;
}
.link:hover {
  font-weight: bold;
}
.link::after {
  content: "";
  position: absolute;
  display: block;
  width: 20px;
  height: 12px;
  top: 25%;
  transition: all 0.2s;
  background-image: url("../../img/arrow-blue.svg");
  background-position: center;
  background-size: cover;
  right: -30px;
}
.link:hover::after {
  right: -35px;
}

.swiper-pagination-bullet {
  background: #7557FF;
  opacity: 1;
  transition: all 0.6s;
  width: 5px;
  height: 5px;
  border-radius: 5px;
}

.swiper-pagination-bullet-active {
  min-width: 23px;
}

.swiper-button-prev,
.swiper-button-next {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  border-radius: 100%;
  background-color: #fff;
  top: 48%;
  box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.2);
  -webkit-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.2);
}
.swiper-button-prev:hover path, .swiper-button-prev:focus path,
.swiper-button-next:hover path,
.swiper-button-next:focus path {
  transition: stroke 0.2s;
  stroke: #7557FF;
}
.swiper-button-prev::after,
.swiper-button-next::after {
  display: none;
}
.swiper-button-prev svg,
.swiper-button-next svg {
  display: block;
  width: 20px;
  height: auto;
}

.swiper-button-prev {
  left: -20px;
}

.swiper-button-next {
  right: -20px;
}
.swiper-button-next svg {
  transform: rotate(180deg);
}

.swiper-pagination {
  bottom: -10px;
}

.swiper {
  overflow: visible;
}

@media (min-width: 1160px) {
  .swiper-slide-prev {
    transition: opacity 0.2s;
    opacity: 0;
  }
}
@media (min-width: 1366px) {
  .swiper-button-prev,
  .swiper-button-next {
